import React, { Component } from 'react'
import './CurtainOverlay.css'

class CurtainOverlay extends Component {
  componentWillMount() {
    this.animationTimeout = setTimeout(this.startAnimation, 1000)
  }
  componentWillUnmount() {
    //lill interval and timeout
    // clearInterval(this.animationInterval)
    // clearTimeout(this.animationTimeout)
  }
  render() {
    
    return (
      <div className="curtain_overlay">
        
      </div>
    )
  }
}


export default CurtainOverlay;



