import React, { Component } from 'react'
import { withRouter } from 'react-router-dom'
import CurtainOverlay from './CurtainOverlay'

class WorkPage extends Component {
  render() {
    return (
      <div className="body">
        Done a lot of work
        <CurtainOverlay/>
      </div>
    )
  }
}

export default withRouter(WorkPage)



