import React, { Component } from 'react'
import { NavLink, Link } from 'react-router-dom';
import {Icon, Image} from 'semantic-ui-react'
import logo from './my-logo2grey.png'
import './Navbar.css'

export default class Navbar extends Component {
  render() {
    return (
      <nav className="main-nav">
        <div className="logo">
          <Link to='/'>
            <Image src={logo}/>
          </Link>
        </div>
        <div className="pages-links">
          <NavLink to='/about' activeClassName="nav-link-active">
            <Icon name="user"/><br/>
            <span className="annotation">ABOUT</span>
          </NavLink>
          <NavLink to='/work' activeClassName="nav-link-active">
            <Icon name="suitcase"/><br/>
            <span className="annotation">WORK</span>
          </NavLink>
          <NavLink to='/contact' activeClassName="nav-link-active">
            <Icon name="address card"/><br/>
            <span className="annotation">CONTACT</span>
          </NavLink>          
        </div>
        <div className="contact-links">
        <a rel="external noreferrer" target="__blank" href='/CV.pdf'><Icon name='id card'/></a>
          <a rel="external noreferrer" target="__blank" href='https://www.linkedin.com/in/roman-belovolov/'><Icon name='linkedin'/></a>
          <a rel="external noreferrer" target="__blank" href='https://www.facebook.com/romanchitto'><Icon name='facebook'/></a>
        </div>
      </nav>
    )
  }
}



